.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 25px;
}
.nav-items {
  list-style: none;
  display: flex;
  text-align: center;
  width: auto;
  margin: 0 1%;
}
.nav-items > li {
  padding: 0 10px;
}
.door {
  height: 150%;
  border-bottom: 1px solid #fff;
  border-radius: 50% 50% 0 0;
  color: #fff;
  padding: 50px 10px 0 10px;
  box-shadow: 0 1px 0 0 rgba(255, 192, 0, 0.25),
    0 -1px 0 0 rgba(255, 64, 0, 0.25), 1px 0 0 0 rgba(255, 192, 0, 0.25),
    -1px 0 0 0 rgba(255, 64, 0, 0.25), 1px -1px 0 0 rgba(255, 128, 0, 0.5),
    -1px 1px 0 0 rgba(255, 128, 0, 0.5), 1px 1px 0 0 rgba(255, 255, 0, 0.75),
    -1px -1px 0 0 rgba(255, 0, 0, 0.75);
}

