.play-card {
  background-color: transparent;
  cursor: pointer;
  display: inline-block;
  flex: 1 1;
  margin: 1%;
  padding: 1%;
  width: 29%;
  height: 250px;
  -webkit-perspective: 1000px;
          perspective: 1000px; /* Remove this if you don't want the 3D effect */
}

/* This container is needed to position the front and back side */
.play-card-inner {
  position: relative;
  border-radius: 3%;
  border: 1px solid rgba(0, 0, 0, 0.4);
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100%;
  text-align: center;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.play-card:hover .play-card-inner,
.play-card.flip .play-card-inner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

/* Position the front and back side */
.play-card-front,
.play-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 1%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.play-card-front {
  background: #2d2d2d url("") 50% 50%;
  border: 3px double #444;
  color: #eeeddd;
}

.play-card-front::before {
  background: #000;
  content: " ";
  display: inline-block;
  height: clamp(10px, 5vh, 60px);
  margin-top: clamp(35%, 20vh, 60%);
  opacity: 0.1;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  vertical-align: middle;
  width: clamp(10px, 5vh, 60px);
  z-index: 1;
}

.play-card-front-title {
  position: absolute;
  border-bottom: 3px #ccc double;
  border-top: 3px #ccc double;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 2;
}

/* Style the back side */
.play-card-back {
  background-color: #fff;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: contain;
  border: 3px double #444;
  color: #111;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
.play-card-back.reverse {
  rotate: 180deg;
}
.play-card-title {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 3px 3px 3px;
  background: rgba(255, 245, 255, 0.7);
  border-top: 3px #999 double;
  padding: 1% 0;
}

